
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}
.background-img {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: cover;
  z-index: -1;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  -webkit-filter: blur(20px);
          filter: blur(20px);
}
